import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Mammals from './panels/Mammals';
import Cat from './panels/Cat';
import Dog from './panels/Dog';
import Dolphins from './panels/Dolphins';
import Lion from './panels/Lion';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'mammals',
			fetchedUser: null,
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Mammals id="mammals" fetchedUser={this.state.fetchedUser} go={this.go} />
				<Cat id="cat" go={this.go} />
				<Dog id="dog" go={this.go} />
				<Dolphins id="dolphins" go={this.go} />
				<Lion id="lion" go={this.go} />
			</View>
		);
	}
}

export default App;
